.inside-room-loading-cointainer h1 {
  font-size: 50px;
  text-justify: center;
  text-align: center;
  margin-top: 30px;
  font-family: 'Luckiest Guy';
  color: rgb(255, 255, 255);
  text-shadow: -4px 3px 2px rgb(0, 0, 0);
}

#loading-game-image {
  height: 60vmin;
  pointer-events: none;  
  justify-items: center;
}

@media (prefers-reduced-motion: no-preference) {
  #loading-game-image {
    -webkit-animation: Loading-img-spin infinite 4s linear;
            animation: Loading-img-spin infinite 4s linear;
    margin: 50px;
    width: 25%;
    height: 25%;
  }
}

@-webkit-keyframes Loading-img-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes Loading-img-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

